import React, { PureComponent } from "react";

import {
  Button,
  Icon,
  Modal,
  Input,
  Select,
  Checkbox,
  ErrorMessage,
} from "components/common";

class Sandbox extends PureComponent {
  state = {
    modal1: false,
    select1: "",
    select2: "",
    checkbox: false,
    checkbox2: false,
    checkbox3: true,
  };

  openModal = modal => () => {
    this.setState({ [modal]: true });
  };

  closeModal = modal => () => {
    this.setState({ [modal]: false });
  };

  render = () => {
    return (
      <React.Fragment>
        <h1>H1 heading</h1>
        <h2>H2 heading</h2>
        <h3>H3 heading</h3>
        <h4>H4 heading</h4>

        <Button.Group>
          <Button color="default">Primary</Button>
          <Button color="primary">Primary</Button>
          <Button color="secondary">Secondary</Button>
          <Button color="success">Success</Button>
          <Button color="error">Error</Button>
          <Button disabled>Disabled</Button>
        </Button.Group>

        <Button.Group>
          <Button variant="text" color="default">
            Primary
          </Button>
          <Button variant="text" color="primary">
            Primary
          </Button>
          <Button variant="text" color="secondary">
            Secondary
          </Button>
          <Button variant="text" color="success">
            Success
          </Button>
          <Button variant="text" color="error">
            Error
          </Button>
          <Button variant="text" disabled>
            Disabled
          </Button>
        </Button.Group>

        <Button.Group>
          <Button variant="icon" icon="burn" color="default" />
          <Button variant="icon" icon="burn" color="primary" />
          <Button variant="icon" icon="burn" color="secondary" />
          <Button variant="icon" icon="burn" color="error" />
          <Button variant="icon" icon="burn" color="success" />
        </Button.Group>

        <div>
          <h1>Icons</h1>
          <h3>Small</h3>
          <Icon size="small" name="user-ninja" />
          <Icon size="small" name="bomb" color="error" />
          <Icon size="small" name="bowling-ball" color="primary" />
          <h3>Default</h3>
          <Icon name="user-ninja" />
          <Icon name="bomb" color="error" />
          <Icon name="bowling-ball" color="primary" />
          <h3>Big</h3>
          <Icon size="big" name="user-ninja" />
          <Icon size="big" name="bomb" color="error" />
          <Icon size="big" name="bowling-ball" color="primary" />
        </div>

        <h1>Modals</h1>
        <Button.Group>
          <Button onClick={this.openModal("modal1")}>Open modal</Button>
        </Button.Group>
        <Modal
          isOpen={this.state.modal1}
          close={this.closeModal("modal1")}
          title="Example modal"
        >
          <Modal.Content>
            <p>
              Wymian zda przerywa pojawienie si wykwintnie ubranej i
              niezwykle urodziwej kobiety  jest to Telimena. Ona tymczasem
              przywitawszy si ze wszystkimi zajmuje miejsce obok Tadeusza.
              Modziecowi zdawao si, e rozpoznaje w niej spotkan rano
              mieszkank jego dawnego pokoju. Patrzy wic z tym wikszym
              zainteresowaniem, a i ona nie pozostawaa na te spojrzenia
              obojtn. W kocu zacza z nim rozmow.
              <br />
              Tymczasem w drugim kocu stou rozgorza na dobre spr Asesora z
              Rejentem o to, czyj chart upolowa zajca. Towarzystwo podzielio
              si na stronnikw Sokoa (pies Asesora) i Kusego (pies Rejenta).
              Spr stara si zaagodzi Podkomorzy, proponujc urzdzenie
              polowania na zajca pod wodz Wojskiego. Ten jednak odmawia
              przewodnictwa, wykadajc zgromadzonym, e zajc nie jest
              zwierzciem godnym organizowania takiej wyprawy. Tym samym
              wymiewa spr Asesora i Rejenta.
              <br />
              Wszyscy rozweseleni odchodz od stow. Tadeusz prowadzi Telimen,
              o ktrej ju wie od Asesora, e jest jego ciotk. Wiadomo ta
              zasmucia i rozdrania modzieca. Chciaby dowiedzie si czego
              wicej, nie byo jednak kogo zapyta, gdy gocie udali si na
              spoczynek.
              <br />W kocowej partii ksigi przeciwstawia narrator picy, a
              wic spokojny dworek szlachecki niepewnemu losowi onierzy,
              walczcych u boku Napoleona. Dowiadujemy si, e wielu z nich
              pozostawia domy rodzinne i przedziera si do Ksistwa
              Warszawskiego, aby mc wstpi do armii lub zosta emisariuszem. W
              Panu Tadeuszu takim emisariuszem jest ksidz Robak. Istotnie
              pojawia si on teraz o tak pnej porze i budzi Sdziego, aby
              poinformowa go o czym. Narrator nie ujawnia jednak treci
              rozmowy.
            </p>
          </Modal.Content>
          <Modal.Buttons>
            <Button variant="text" onClick={this.closeModal("modal1")}>
              Close
            </Button>
            <Button onClick={this.closeModal("modal1")}>OK!</Button>
          </Modal.Buttons>
        </Modal>

        <div>
          <h1>Inputs</h1>
          <Input icon="envelope" type="email" />

          <Input icon="user-nurse" type="text" error="Contact a doctor" />

          <Input
            icon="stop-circle"
            type="text"
            placeholder="Disabled"
            disabled
          />

          <Input icon="broom" type="text" fullWidth placeholder="Brooooom" />

          <Input type="password" fullWidth />
        </div>

        <h1>Select</h1>
        <Select
          placeholder="Select"
          value={this.state.select1}
          options={[
            { value: "a", text: "Afganistan" },
            { value: "b", text: "Belgia" },
            { value: "c", text: "Cypr" },
          ]}
          onChange={value => this.setState({ select1: value })}
        />
        <Select
          fullWidth
          placeholder="Fullwidth Select"
          value={this.state.select2}
          options={[
            { value: "a", text: "Afganistan" },
            { value: "b", text: "Belgia" },
            { value: "c", text: "Cypr" },
          ]}
          onChange={value => this.setState({ select2: value })}
        />

        <h1>Checkbox</h1>
        <Checkbox
          checked={this.state.checkbox}
          onChange={e => this.setState({ checkbox: e.target.checked })}
        />
        <br />
        <Checkbox
          label="Label"
          checked={this.state.checkbox2}
          onChange={e => this.setState({ checkbox2: e.target.checked })}
        />
        <br />
        <Checkbox
          label="Disabled"
          disabled
          checked={this.state.checkbox3}
          onChange={e => this.setState({ checkbox3: e.target.checked })}
        />

        <h1>Error Message</h1>
        <ErrorMessage content="You can't do that!" fullWidth />
        <ErrorMessage
          content="This HTML file is a template.
      If you open it directly in the browser, you will see an empty page.

      You can add webfonts, meta tags, or analytics to this file.
      The build step will place the bundled scripts into the <body> tag."
        />
      </React.Fragment>
    );
  };
}

export default Sandbox;
